'use strict';

import mongoose from 'mongoose';

var NewsSchema = new mongoose.Schema({
  thumb: {
    type:String
  },
  title: {
  	type:String
  },
  shortDescription: {
  	type:String
  },
  fullDescription: {
  	type:String
  },
  createdAt: {
    type: Date,
    default: Date.now
  },
  updatedAt: { 
    type: Date 
  }
});

export default mongoose.model('News', NewsSchema);
